.main-footer {
  @media (min-width: @desktop-width) {
    position: relative;
  }
}

.main-footer-background {
  background-color: @basic-grey;
}

.main-footer__container {
  @media (min-width: @tablet-width) {
    width: 708px;
    margin: 0 auto;
  }

  @media (min-width: @desktop-width) {
    width: unset;
    max-width: 1220px;
  }
}

.main-footer__links {
  padding-top: 39px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 22px;

  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: @tablet-width) {
    width: 708px;
    margin: 0 auto;
    padding: 63px 30px 55px 30px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    margin: 0 auto;
    width: 1220px;
    align-items: unset;
  }
}

.main-footer__links-logo {
  position: relative;
  padding-bottom: 32px;

  &::after {
    content: "";
    position: absolute;
    width: 280px;
    height: 1px;
    background-color: @gray;
    top: 56px;
    left: -70px;
  }

  @media (min-width: @tablet-width) {
    padding: 0;

    &::after {
      display: none;
    }
  }
}

// стилизация состояния для логотипа футера
.main-footer__links-logo:hover {
  opacity: 0.8;
}

.main-footer__links-logo:active {
  opacity: 0.3;
}


// стили для карты

.main-footer__adress-footer-map iframe {
  border: none;
  display: block;
  width: 100%;
  height: 362px;

  @media (min-width: @tablet-width) {
    height: 400px;
  }
}
